import Bmob from "hydrogen-js-sdk"
import {genUpToken} from '../../../../../util/qiniuToken.js'
import qiniuUploader from '../../../../../util/qiniuUploader.js'
export default{
	data(){
		return{
			bodyValue:'',//body
			imgList:[],
			isAddTrue:true,
			objectId:'',
			token:'',
			imgListUrl:[]
		}
	},
	onLoad:function(option){
		this.objectId=option.objectId;
		this.initQiniu();
		this.init();
	},
	onShow:function(){
		this.initQiniu();
		this.init();
	},
	methods:{
		init(){
			var THAT = this;
			const query = Bmob.Query('MicroBlog');
			query.get(this.objectId).then(res => {
			  console.log(res)
			  THAT.bodyValue = res.content;
			  THAT.imgList = res.imgList.split(",");
			  THAT.imgListUrl = res.imgList.split(",")
			}).catch(err => {
			  console.log(err)
			})
		},
		initQiniu(){
			var token;
			var policy = {};
			var bucketName = 'xch752';
			var AK ='T0c0qxTfHE07tX8QdKxtSaBAvaj6J5b_-HlQYTgf';
			var SK = 'bT-PoAdQusOcadeX3ELoFPRGDKJHr8d8kYBmBQ5X';
			var deadline = Math.round(new Date().getTime()/1000) + 3600;
			policy.scope = bucketName;
			policy.deadline = deadline;
			token=genUpToken(AK,SK,policy);
			this.token=token;
		},
		clearAll(){
			this.bodyValue='';
			this.imgList=[];
			this.isAddTrue=true;
			this.imgListUrl=[];
		},
		submitMSG(){
			var THAT = this;
			if((!this.bodyValue)&&(this.imgList.length==0)){
				uni.showModal({
					title:'提示',
					content:'发送内容不能为空',
					showCancel:false
				})
			}else{
				//修改代码
				const query = Bmob.Query('MicroBlog');
				query.get(THAT.objectId).then(res => {
				  console.log(res)
				  res.set('content',THAT.bodyValue);
				  res.set('imgList',THAT.imgListUrl.join());
				  res.save()
				  uni.navigateBack({
				  	delta:1,
					success:(res)=>{
						console.log(res);
					}
				  })
				}).catch(err => {
				  console.log(err)
				})
			}
		},
		chooseImg(){
			var THAT = this;
			uni.chooseImage({
				count:1,
				sizeType:['original','compressed'],//原图&压缩
				sourceType:['album','camera'],//相册&相机
				success: (res) => {
					if(THAT.imgList.length<9){
						THAT.imgList.push(res.tempFilePaths[0]);
						var filePath = res.tempFilePaths[0]
						qiniuUploader.upload(filePath, (res) => {
							THAT.imgListUrl.push(res.fileUrl);
							console.log(`this.imgList:${THAT.imgList} ${typeof THAT.imgList}`)
							console.log(`this.imgListUrl:${THAT.imgListUrl} ${typeof THAT.imgListUrl}`)
							console.log('file url is: ' + res.fileUrl);
						}, (error) => {
							console.log('error: ' + error);
						}, {
						  region: 'SCN',
						  domain: 'http://pxcfst1g4.bkt.clouddn.com', // // bucket 域名，下载资源时用到。如果设置，会在 success callback 的 res 参数加上可以直接使用的 ImageURL 字段。否则需要自己拼接
						  key: '', // [非必须]自定义文件 key。如果不设置，默认为使用微信小程序 API 的临时文件名
						  // 以下方法三选一即可，优先级为：uptoken > uptokenURL > uptokenFunc
						  uptoken: THAT.token, // 由其他程序生成七牛 uptoken
						}, (res) => {
							// console.log(res);
							// if(res.progress!==100){
							// 	uni.showToast({
							// 		title:`上传中，已上传${res.progress}`,
							// 		icon:'loading'
							// 	})
							// }else if(res.progress===100){
							// 	uni.showToast({
							// 		title:`上传完成`,
							// 		icon:'success'
							// 	})
							// }
							// console.log('上传进度', res.progress)
							// console.log('已经上传的数据长度', res.totalBytesSent)
							// console.log('预期需要上传的数据总长度', res.totalBytesExpectedToSend)
						}, () => {
						  // 取消上传
						}, () => {
						  // `before` 上传前执行的操作
						}, (err) => {
						  // `complete` 上传接受后执行的操作(无论成功还是失败都执行)
						});
					}
					if(THAT.imgList.length>=9){
						THAT.isAddTrue=false;
					}
					console.log(THAT.imgList)
				},
				fail: (err) => {
					console.log(err)
				}
			})
		},
		deleteImg(index){
			this.imgList.splice(index,1);
			this.imgListUrl.splice(index,1);
			if(this.imgList.length<9){
				this.isAddTrue=true;
			}else{
				this.isAddTrue=false;
			}
			console.log(`this.imgList:${this.imgList} ${typeof this.imgList}`)
			console.log(`this.imgListUrl:${this.imgListUrl} ${typeof this.imgListUrl}`)
		}
	}
}
